body {
  // height: 97vh;
  // background-color: red;

  margin: 80px 0;
}

#root {
  position: relative;

  max-width: 540px;
  height: 100%;
  margin-right: auto;
  margin-left: auto;
  padding: 10px;

  background-color: rgb(230, 235, 240);
}

header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  min-height: 80px;

  background-color: rgba(100, 100, 100, 0.5);

  text-align: center;
}

.logo {
  font-size: 80px;
  color: white;

  text-decoration: none;
}

.add-todo {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;

  font-size: 48px;
  color: white;

  width: 100%;
  min-height: 80px;

  background-color: rgba(100, 255, 100, 0.5);
  border: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  padding: 10px;

  background-color: rgb(200, 200, 200);
}

li:not(:last-child) {
  margin-bottom: 10px;
}
